tent":["\n.main-wrapper {\n  background: #fff;\n  width: 92%;\n  margin: 0 auto;\n  border-radius: 4px;\n  overflow: hidden;\n  padding: 24px 0 0;\n  margin-bottom: 24px;\n  position: relative;\n}\n.main-container {\n  padding: 0 6% 120px 4% !important;\n  margin-left: -1px;\n  min-height: 500px;\n  border-left: 1px solid #e9e9e9;\n}\n.page-container {\n  /*color: #666;*/\n  font-size: 14px;\n  line-height: 1.5;\n}\n.page-container h1 {\n  color: #404040;\n  font-weight: 500;\n  line-height: 40px;\n  margin-bottom: 24px;\n  margin-top: 8px;\n  font-size: 28px;\n  font-family: lato, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;\n}\n.page-container > h2 {\n  font-size: 24px;\n}\n.page-container hr {\n  border-radius: 10px;\n  height: 3px;\n  border: 0;\n  background: #eee;\n  margin: 20px 0;\n}\n.page-container code,\n.page-container kbd,\n.page-container pre,\n.page-container samp {\n  font-family: Consolas, monospace;\n}\n.page-container code {\n  margin: 0 3px;\n}\n.page-container > ul li {\n  list-style: circle;\n  margin-left: 20px;\n}\n.page-container > ol li {\n  list-style: decimal;\n  margin-left: 20px;\n  padding-left: 8px;\n}\n.page-container .table {\n  font-size: 13px;\n  border-collapse: collapse;\n  border-spacing: 0px;\n  empty-cells: show;\n  border: 1px solid #e9e9e9;\n  width: 100%;\n  margin: 16px 0;\n}\n.page-container .table th {\n  background: #F7F7F7;\n  white-space: nowrap;\n  color: #5C6B77;\n  font-weight: 600;\n}\n.page-container .table td:first-child {\n  background: #fcfcfc;\n  font-weight: 500;\n  width: 20%;\n  font-family: \"Lucida Console\", Consolas, Menlo, Courier, monospace;\n}\n.page-container .table th,\n.page-container .table td {\n  border: 1px solid #e9e9e9;\n  padding: 8px 16px;\n  text-align: left;\n}\n.main-container > section > h3 {\n  color: #404040;\n  font-family: lato, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;\n  margin: 1.6em 0 .6em;\n  font-weight: 500;\n  clear: both;\n}\n.code-boxes-col-2-1 {\n  display: inline-block;\n  vertical-align: top;\n  padding: 0 8px;\n}\n.aside-container {\n  padding-bottom: 50px;\n}\n.aside-container .ant-menu-item a,\n.aside-container .ant-menu-submenu-name span,\n.aside-container > .ant-menu-item {\n  font-size: 14px;\n  text-overflow: ellipsis;\n  overflow: hidden;\n}\n.header-anchor {\n  margin-left: -18px;\n}\n.markdown > ul {\n  margin-top: 1rem;\n  margin-bottom: 1rem;\n}\n.markdown > ul > li {\n  list-style: circle;\n  margin-left: 1rem;\n}\n.markdown h1 a,\n.markdown h2 a,\n.markdown h3 a {\n  font-size: .8em;\n  opacity: 0;\n  font-weight: normal;\n  transition: opacity 0.2s ease-in-out;\n}\n.markdown h1:hover a,\n.markdown h2:hover a,\n.markdown h3:hover a {\n  opacity: 1;\n}\n.markdown blockquote {\n  font-size: 90%;\n  color: #999;\n  border-left: 4px solid #e9e9e9;\n  padding-left: 0.8em;\n  margin: 1em 0;\n}\n.markdown code {\n  margin: 0 1px;\n  background: #f7f7f7;\n  padding: .2em .4em;\n  border-radius: 3px;\n  font-size: .9em;\n  border: 1px solid #eee;\n}\n.markdown > p,\n.markdown > pre {\n  margin-top: 1rem;\n  margin-bottom: 1rem;\n}\n"],"sourceRoot":""}]);

